import React from 'react';

import './second-registration-step.scss';

const SecondRegistrationStep = () => {
    return (
        <>
            <h2>Next steps</h2>
            <p className={`next-step`}>You have successfully registered!</p>
            <p className={`suggestion-to-visit`}>Please, visit your email to activate your account.</p>
        </>

    );

}

export default SecondRegistrationStep;